import {NavLink} from "react-router-dom";
import React, {FC, useState} from "react";
import {Button, Checkbox, message, Modal} from "antd";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../redux/store";
import {appActions} from "../../redux/app-reducer";
import { HeartTwoTone } from "@ant-design/icons";

export const MenuPage = () => {

  const dispatch = useDispatch()
  //      . . .
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [isChangeModalVisible, setIsChangeModalVisible] = useState(false)
  //    . . .
  const isInstructionCompleted = useSelector((state: AppStateType) => state.app.isInstructionCompleted)

  const info = () => {
    message.warning('    (')
  }

  const showModal = () => {
    setIsModalVisible(() => true)
  }
  const handleOk = () => {
    setIsModalVisible(false)
  }

  const handleCancel = () => {
    setIsModalVisible(false)
  }

  const showChangesModal = () => {
    setIsChangeModalVisible(() => true)
  }
  const handleChangesOk = () => {
    setIsChangeModalVisible(false)
  }


  return (
    <>
        <div className='menu bannerBack'>
          <Modal className='menuModal' title="" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel} okText='' footer={[
            <>
              <Checkbox onChange={() => dispatch(appActions.setIsInstructionCompleted())}>  </Checkbox>
              <NavLink to='/profile'>
                <Button type='primary' onClick={handleOk}>
                  
                </Button>
              </NavLink>
            </>
          ]}>
            <p><h3><b>    </b></h3></p>
            <p>   <b>10000$</b>   ,     ,    ! <i>(    <b></b>   !)</i></p>
            <p>     </p>
            <ul>
              <li><b></b> ( )</li>
              <li><b></b> (   <b>500$</b>)</li>
              <li><b> </b> (   <b>3000$</b>)</li>
            </ul>
            <p>
                   <b></b>,       <b></b> <i>(   ,      )</i>
            </p>
            <p>
                        .
              (    <b><i></i></b>)
            </p>
            <p>
                   . ( <b><i></i></b>)
              <br/>
                 :
              <ul>
                <li>
                     <b></b>
                </li>
                <li>
                      <b></b>
                </li>
                <li>
                       <b></b>
                </li>
              </ul>
            </p>
          </Modal>
          <Modal title='' visible={isChangeModalVisible} footer={[
            <>
              <NavLink to='/rofl'>
                <Button onClick={handleChangesOk}>
                   
                </Button>
              </NavLink>
              <Button type='primary' onClick={handleChangesOk}>
                
              </Button>
            </>
          ]}>
            <ol>
              <li>     <b></b>  (- )</li>
              <li>   <b></b></li>
              <li>  <b></b>  <b></b> </li>
              <li> -  - <b>3</b>, (      )</li>
              <li>  ,      <b></b>  <b> </b>      .</li>
              <li>  <b></b>,   </li>
              <li> <b></b>   </li>
              <li> <b></b>  <b>""</b>     : <a href="https://vk.com/drunua"></a></li>
            </ol>
            <i>p.s.    <HeartTwoTone twoToneColor="#eb2f96" /></i>
          </Modal>
          <div className="menuGameName">
            <span>#</span>
            <div><small><i>ALPHA 1.2.1</i></small></div>
          </div>
          <div className="menuContent">
            <div className="menuHeader">
              
            </div>
            <div className="menuList">
              <div className="menuListItem">
                {isInstructionCompleted
                  ? <NavLink to='/profile'>
                      <button className='menuListItem__Button'>
                         
                      </button>
                    </NavLink>
                  : <button className="menuListItem__Button" onClick={showModal} >
                       
                    </button>
                }
              </div>
              <div className="menuListItem">
                <button className="menuListItem__Button" onClick={showChangesModal}>
                   
                </button>
              </div>
              <div className="menuListItem">
                {/*<NavLink to='/game'>*/}
                {/*  <button className='menuListItem__Button'>*/}
                {/*    */}
                {/*  </button>*/}
                {/*</NavLink>*/}
                <button className="menuListItem__Button" onClick={info}>
                  
                </button>
              </div>
              <div className="menuListItem">
                {/*<NavLink to='/game'>*/}
                {/*  <button className='menuListItem__Button' disabled={true}>*/}
                {/*    */}
                {/*  </button>*/}
                {/*</NavLink>*/}
                <button className="menuListItem__Button" onClick={info}>
                  
                </button>
              </div>
              <div className="menuListItem">
                <NavLink to='/settings'>
                  <button className='menuListItem__Button'>
                    
                  </button>
                </NavLink>
              </div>
            </div>
          </div>
        </div>
    </>
  )
}